h3 {
  display: inline-block;
  color: brown;
  margin: 0 10px 0 0;
}
.todolistTitle__btn {
  color: white;
}

.error {
  border: 2px solid red;
  border-radius: 0 5px 0 5px;
}

.block {
  display: block;
  height: 30px;
  border: none;
  color: red;
}


/*//======input active task=======================================*/
ul {
  padding: 0;
  margin: 30px 0 0 0;
}


/*========block input tasks================================================*/
.input__block {
  display: flex;
  align-items: center;
}

.dellTask {
  background: url("data:image/svg+xml;base64,")
    center/cover no-repeat;
  width: 30px;
  height: 30px;
}
.dellTask:hover {
  background: url("data:image/svg+xml;base64,")
    center/cover no-repeat;
}

.addTask {
  background: url("data:image/svg+xml;base64,")
    center / cover no-repeat;
  width: 30px;
  height: 30px;
  -webkit-perspective: 1000px;
  -webkit-backface-visibility: hidden;
}
.addTask:hover {
  background: url("data:image/svg+xml;base64,")
    center / cover no-repeat;
  -webkit-transform: scale(1.1);
}
.addTask:active {
  -webkit-transform: scale(0.9);
  transition: all 0.01s ease;
}
